<template>
  <div>
    <canvas width="600" height="500" ref="test" />
  </div>
</template>
<script>
import Bounce from "../service/bounce";

export default {
  mounted() {
    this.Bounce = new Bounce(this.$refs.test);
    this.BounceAnimation();
  },
  data() {
    return {
      Bounce: null,
    };
  },
  methods: {
    BounceAnimation() {
      this.Bounce.update();
      this.Bounce.draw();
      requestAnimationFrame(this.BounceAnimation);
    },
  },
};
</script>