// concepts  velocity = displacement/time;
//           accelartion = velocity/time
// losses -> friction(movement on surface), elasticty(colliding with ground)

import Canvas from "./canvas";
export default class Bounce {
    constructor(canvas, x, y) {
        this.x = x || Math.random() * canvas.width;
        this.y = y || 0;
        this.canvas = canvas;
        this.deltaTime = 1;
        this.xVelocity = 5;
        this.yVelocity = 5,
            this.xAccelration = 0.2;
        this.yAccelration = 1;
        this.bounceCanvas = null;
        this.canvas = null;
        this.bounce = 0.9;
        this.friction = 0.8;
        this.Ccanvas = new Canvas();
        this.canvas = canvas;
        this.context = this.Ccanvas.createCanvas(this.canvas, '2d');
    }
    draw() {
        this.context.fillColor = "red";
        this.context
            .clearRect(0, 0, 600, 500).beginPath()
            .createArc(this.x, this.y, 30, 0, 360)
            .fill()
            .strokeRect(0, 0, 600, 500);

    }
    update() {
        if (this.y + 30 >= this.canvas.height) {
            this.yVelocity *= -this.bounce;
            this.y = this.canvas.height - 30;
            this.xVelocity *= this.friction;
        }
        // top
        if (this.y - 30 <= 0) {
            this.yVelocity *= -this.bounce;
        }

        // // left
        if (this.x - 30 <= 0) {
            this.xVelocity *= -this.friction;
        }
        // // right
        if (this.x + 30 >= this.canvas.width) {
            this.xVelocity *= -this.friction;
        }

        // this.xVelocity += this.xAccelration * this.deltaTime;
        this.yVelocity += this.yAccelration * this.deltaTime;
        this.x += this.xVelocity * this.deltaTime;
        this.y += this.yVelocity * this.deltaTime;
    }
}