export default class Canvas {
    canvasElement = null;
    context = null;
    createCanvas(dom, type) {
        if (dom) {
            this.canvasElement = dom;
            this.context = dom.getContext(type)
        }
        return this;
    }
    createArc(x, y, r, start, end, isClockWise = false) {
        this.context.arc(x, y, r, start, (Math.PI / 180) * end, isClockWise);
        return this;
    }
    createLine(mx1, my1, lx1, ly1) {
        this.context.moveTo(mx1, my1);
        this.context.lineTo(lx1, ly1);
        return this;
    }

    translate(x = 0, y = 0) {
        this.context.translate(x, y);
        return this;
    }

    moveTo(x = 0, y = 0) {
        this.context.moveTo(x, y);
        return this;
    }

    scale(x = 0, y = 0) {
        this.context.scale(x, y);
        return this;
    }

    transform(hs, hsk, vsk, vs, hm, vy) {
        this.context.transform(hs, hsk, vsk, vs, hm, vy);
        return this;
    }

    rotate(deg) {
        this.context.rotate((Math.PI / 180) * deg);
        return this;
    }

    createBezierCurve(mx, my, cpx1, cpy1, cpx2, cpy2, x, y) {
        this.context.moveTo(mx, my);
        this.context.bezierCurveTo(cpx1, cpy1, cpx2, cpy2, x, y);
        return this;

    }


    set fillColor(color) {
        this.context.fillStyle = color;
    }

    set strokeColor(color) {
        this.context.strokeStyle = color;
    }

    save() {
        this.context.save();
        return this;
    }

    beginPath() {
        this.context.beginPath();
        return this;
    }

    restore() {
        this.context.restore();
        return this;
    }

    fill() {
        this.context.fill();
        return this;
    }

    stroke() {
        this.context.stroke();
        return this;
    }

    closePath() {
        this.context.closePath();
        return this;
    }

    fillRect(x, y, width, height) {
        this.context.fillRect(x, y, width, height);
        return this;
    }

    strokeRect(x, y, width, height) {
        this.context.strokeRect(x, y, width, height);
        return this;
    }

    clearRect(x, y, width, height) {
        this.context.clearRect(x, y, width, height);
        return this;
    }

    drawImage(img,x,y,width,height) {
        this.context.drawImage(img,x,y,width,height);
        return this;
    }
    getImageData(x,y,width,height){
       return this.context.getImageData(x,y,width,height);
        
    }
}